import React from "react";
import "./Nav.css";
import { NavLink } from "react-router-dom";

function Nav() {
  return (
    <div className="nav">
      <div className="wrapper">
        <NavLink activeClassName="active" className="link" to="/beelinesite">
          <img
            className="nav_logo"
            src="https://beeline.uz/webfiles/1599801229801/dist/images/logo.svg"
            alt="Logo"
          />
        </NavLink>
        <ul className="nav_menu">
          <NavLink
            exact
            activeClassName="active"
            className="home-link link"
            to="/beelinesite"
          >
            <li>
              <i className="fa fa-home"></i>
            </li>
          </NavLink>
          <NavLink
            exact
            activeClassName="active"
            className="link"
            to="/tariffs"
          >
            <li>
              <i className="fas fa-table"></i>
            </li>
          </NavLink>
          <NavLink exact activeClassName="active" className="link" to="/help">
            <li>
              <i className="far fa-question-circle"></i>
            </li>
          </NavLink>
          <li>
            <i className="fas fa-search"></i>
          </li>
          <NavLink activeClassName="active" className="link" to="/cabinet">
            <li>
              <i className="fas fa-user-lock"></i>
            </li>
          </NavLink>
          
          <li
            className="menu-pc"
            onClick={() => {
              const getIcon = document.querySelector(".icon");
              const getMenu = document.querySelector(".open-menu");
              if (getIcon.className === "fas icon fa-ellipsis-h") {
                getIcon.className = "fas icon fa-ellipsis-h active";
                getMenu.style.display = "block";
                setTimeout(() => {
                  getMenu.style.opacity = "1";
                }, 300);
                setTimeout(() => {
                  getMenu.style.boxShadow = '0px 5px 23px -2px rgba(0, 0, 0, 0.14)'
                }, 500);
                
              } else {
                getIcon.className = "fas icon fa-ellipsis-h";
                getMenu.style.opacity = "0";
                getMenu.style.boxShadow = 'none'
                setTimeout(() => {
                  getMenu.style.display = "none";
                }, 300);

              }
            }}
          >
            <i className="fas icon fa-ellipsis-h"></i>
          </li>
        </ul>
      </div>
      <div className="open-menu"></div>
    </div>
  );
}

export default Nav;
